import useInput from '@hooks/useInput';
import {
  BlankDiv,
  Button,
  Error,
  Form,
  Header,
  IconButton,
  Input,
  InputContainer,
  Label,
  LinkContainer,
  LogoWrapper,
} from '@pages/member/SignUp/styles';
import React, { useCallback, useEffect, useState } from 'react';
import useCustomMember from '@hooks/useCustomMember';
import { Link } from 'react-router-dom';
import { toast } from 'react-toastify';
import { AiOutlineEye, AiOutlineEyeInvisible } from 'react-icons/ai';
import logo from '../../../unitek_ndt_logo.png';

const LogIn = () => {
  const { isLogin, doLogin, updateUrlAfterLogin } = useCustomMember();

  const [logInError, setLogInError] = useState(false);
  const [email, onChangeEmail] = useInput('');
  const [password, onChangePassword] = useInput('');
  const [showPassword, setShowPassword] = useState(false);

  const togglePasswordVisibility = () => {
    setShowPassword((prev) => !prev);
  };

  const onSubmit = useCallback(
    async (e: React.FormEvent<HTMLFormElement>) => {
      e.preventDefault();
      setLogInError(false);

      try {
        const result = await doLogin({ email, password });

        if (result.success) {
          toast.dismiss();
          toast.success('Login successful');
          await updateUrlAfterLogin();
        } else {
          setLogInError(true);
          toast.error(result.message || 'Login failed');
        }
      } catch (error) {
        setLogInError(true);
        console.error('Login error:', error);
        toast.error('Login failed');
      }
    },
    [doLogin, email, password, updateUrlAfterLogin],
  );

  useEffect(() => {
    if (isLogin) {
      updateUrlAfterLogin().then();
    }
  }, [isLogin, updateUrlAfterLogin]);

  return (
    <div id="container">
      <LogoWrapper>
        <img src={logo} alt="Company Logo" />
        <BlankDiv></BlankDiv>
      </LogoWrapper>
      {/*<Header>MQ-Navigator</Header>*/}
      <Form onSubmit={onSubmit}>
        <Label id="email-label">
          <span>Email Address</span>
          <InputContainer>
            <Input type="email" id="email" name="email" value={email} onChange={onChangeEmail} />
            <BlankDiv></BlankDiv>
          </InputContainer>
        </Label>
        <Label id="password-label">
          <span>Password</span>
          <InputContainer>
            <Input
              type={showPassword ? 'text' : 'password'}
              id="password"
              name="password"
              value={password}
              onChange={onChangePassword}
            />
            <IconButton type="button" onClick={togglePasswordVisibility}>
              {showPassword ? <AiOutlineEyeInvisible size={20} /> : <AiOutlineEye size={20} />}
            </IconButton>
          </InputContainer>
          {logInError && <Error>The combination of email and password does not match.</Error>}
        </Label>
        <Button type="submit">Log In</Button>
      </Form>
      <LinkContainer>
        Not a member yet?&nbsp;
        <Link to="/member/signup">Sign Up Here</Link>
      </LinkContainer>
    </div>
  );
};

export default LogIn;
